
import React, { Component } from "react";
import {
    Icon,
    Table,
    Popconfirm,
    Tooltip,
} from "antd";
import styles from "./index.css";
import { View } from "react-web-dom";
import NodeModal from "../nodeModal";
import { routerConfig } from "../../index/layout/routerConfig";

const routerData = routerConfig.routers;

export default class SettingNodeTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            editNodeVisible:false,
            currentNode:{},
            key:null,
        };
    }
    render() {
        // const {
        //     tree
        // } = this.props
        const {
            editNodeVisible,
            currentNode
        } = this.state
        // tree.map((item,index)=>{
        //     item['children']=item['_child']
        // })
        // console.log('tree',tree);
        // console.log('SettingNodeTable',this.props);
        const columns = [
            {
                title: "",
                dataIndex: "title",
            },
            {
                title: "",
                dataIndex: "hideInMenu",
                className: styles.iconcolumn,
                render: (text) => {
                    return !text
                    ?   <Icon type="check" />
                    :   <Icon type="close" style={{color:'red'}}/>
                }
            },
            {
                title: "",
                key: "operation",
                render: (text, record) => {
                    return (
                        <View className={styles.operation}>
                            <a
                                onClick={()=>{
                                    this.setState({
                                        currentNode:record,
                                        editNodeVisible:true,
                                    })
                                }}
                            >
                                
                            </a>

                            {
                                currentNode==record ?
                                <NodeModal
                                    {...this.props}
                                    type='edit'
                                    pidShow={currentNode.pid===0 ? false : true}
                                    nodeVisible={editNodeVisible}
                                    nodeCancel = {()=>{
                                        this.setState({editNodeVisible:false})
                                    }}
                                    initialValue={currentNode}
                                    routerData={routerData}
                                /> : null
                            }
                            {
                                record.children&&record.children.length ? <Tooltip title=''>
                                    <a style={{color:'#ccc'}}></a>
                                </Tooltip> : <Popconfirm
                                    title="Sure to delete?"
                                    onConfirm={() => {
                                        this.props.delAuthRuleTree({id:record.id})
                                    }}
                                >
                                    <a></a>
                                </Popconfirm>
                            }
                        </View>
                    );
                },
                className: styles.column
            }
        ];
        return (
            <Table
                bordered
                columns={columns}
                // defaultExpandAllRows
                rowKey={record => record.path}
                dataSource={routerData}
                pagination={{
                    showQuickJumper: true,
                    // total: total_number,
                }}
                loading={false}
            />
        );
    }
}
